.t{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border: 1px solid #999;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.t-icon{
width: 15px;
height: 15px;
}

.t-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
    
}

@media screen and (max-width:500px) {
    .t{
        width: 40px;
        height: 17px;
        border-radius: 20px;
        border: 1px solid #999;
        background-color: white;
        position: fixed;
        top: 10px;
        right: 10px;
        z-index: 999;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
    
    .t-icon{
    width: 10px;
    height: 14px;
    }
    
    .t-button{
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: #999;
        position: absolute;
        cursor: pointer;
        
    }
    
}